import React, {useEffect, useState} from 'react';
import ErrorComponent from './ErrorComponent';
import axios from 'axios';
import {server} from "../index"
import Loader from './Loader';
import CoinCard from './CoinCard';

import { Button, Container, HStack, RadioGroup, Radio } from '@chakra-ui/react';

const Coins = () => {
  const [coins, setCoins] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [page, setPage] = useState(1);
  const [currency, setCurrency] = useState("inr");

  const currencySymbol = currency === "inr" ? "" : currency==="eur"?"":"$";

  const changePage=(page)=>{
    setPage(page);
    setLoading(true);
  }

  const btns = new Array(15).fill(1);


  useEffect(() => {

    const fetchCoins = async() =>{
     try {
      const { data } = await axios.get(`${server}/coins/markets?vs_currency=${currency}&page=${page}`)
      
      setCoins(data);
      setLoading(false);

     } 
     catch (error) {
      setError(true);
      setLoading(false);
     }
    }

    fetchCoins();
  }, [currency, page])

  if(error) return <ErrorComponent message={"Something went Wrong"}/>

  return (
    <Container maxW={"container.xl"}>
      {loading ? ( <Loader /> )  : (
         <>
       
         <RadioGroup value={currency} onChange={setCurrency} p={"4"} >
          <HStack spacing={4} justifyContent={"center"}>
            <Radio value={"inr"}></Radio>
            <Radio value={"usd"}>$</Radio>
            <Radio value={"eur"}></Radio>
          </HStack>
         </RadioGroup>

      <HStack wrap={"wrap"} justifyContent={"space-evenly"}>
        {
          coins.map((i) => (
            <CoinCard 
            id={i.id}
            key={i.id}
            name={i.name}
            price={i.current_price}
            img = {i.image}
            symbol = {i.symbol}
            currencySymbol={currencySymbol}
            />
          ))
        }
      </HStack>
      <HStack w={"full"} overflowX={"auto"} p={"8"}>
        {
          btns.map((item, index) =>(
            <Button 
            key={index}
            bgColor={"blackAlpha.900"} color={"whiteAlpha.900"} onClick={() =>changePage(index+1)}>
          {index + 1}
        </Button>
          ))
        }
      </HStack>
      </>)}
    </Container>
  )
}





export default Coins
